* {
  box-sizing: border-box;
}
body {
  box-sizing: border-box;
  background-color: #f9f8f6;
  font-family: 'Roboto', sans-serif;
}
* {
  box-sizing: inherit;
}
.hello__title,
.answer__title {
  margin: 65px 0;
  font-size: 38px;
  font-weight: 500;
  letter-spacing: 3px;
}
.hello__title--icon:before,
.answer__title--icon:before {
  display: inline-block;
  width: 44px;
  height: 42px;
  margin-right: 5px;
  content: '';
  background-image: url("../img/october.png");
  background-repeat: no-repeat;
  background-position: center;
}
.item {
  position: relative;
  top: 0;
  width: 100%;
  margin-bottom: 35px;
  text-align: center;
}
.item__face {
  position: relative;
  height: 292px;
  margin-bottom: 15px;
  background-color: #fff;
  overflow: hidden;
}
.item__face:before {
  display: inline-block;
  width: 0;
  height: 100%;
  content: '';
  vertical-align: middle;
}
.item__img {
  vertical-align: middle;
}
.item__index {
  position: absolute;
  opacity: 0;
}
.item__index--visible {
  display: inline-block;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 65px;
  height: 65px;
  margin: auto;
  font-size: 38px;
  font-weight: 700;
  color: #fff;
  line-height: 65px;
  vertical-align: middle;
  background-color: rgba(213,34,61,0.9);
  opacity: 1;
}
.item__title {
  margin-bottom: 10px;
  font-size: 16px;
}
.item__desc {
  min-height: 60px;
  padding: 0 20px;
  font-size: 14px;
}
.item__buy {
  width: 100%;
  border-radius: none;
  border: none;
  background-color: #d5223d;
  font-size: 14px;
  color: #fff;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: uppercase;
  overflow: hidden;
  opacity: 0;
}
.item:hover {
  position: absolute;
  top: -25px;
  left: -100px;
  right: -100px;
  width: 116%;
  margin: auto;
  padding: 20px;
  border: 5px solid #d5223d;
  background-color: #fff;
  box-shadow: 0 6px 20px rgba(0,0,0,0.2);
  z-index: 1;
}
.item:hover .item__desc {
  margin-bottom: 20px;
  padding: 0 32px;
}
.item:hover .item__buy {
  padding: 15px;
  opacity: 1;
}
.answer {
  margin-bottom: 100px;
}
